.main{
    display: flex;
    justify-content: flex-start;
    flex-direction: column;
    align-items: flex-start;
    box-sizing: border-box;
    text-align: left;
}

.main > *{
    margin: 1em 0;
    margin-top: 0;
    width: 100%;
    padding: 0 1rem;
    box-sizing: border-box;
}

@media only screen and (max-width: 1500px) {
    .main > *{
        padding: 0 1.5rem;
    }    
}

@media only screen and (max-width: 1024px) {
    .main > *{
        padding: 0 20px;
    }    
}

.main > *:first-child{
    margin-top: 1em;
}

.variable{
    padding: 0 1em 2em 1em;
    position: relative;
}

.variable::after{
    position: absolute;
    content: "";
    background-color: var(--secondary-color-50a);
    opacity: .5;
    height: 1px;
    top: -1em;
    left: 1em;
    right: 1em;
}