.main{
    height: 2.3125em;
    margin: 0 .625em;
    padding: 0rem .625em;
    background-color: var(--secondary-color);
    color: var(--primary-color);
    border-width: 0.125em;
    border-style: solid;
    border-color: var(--secondary-color);
    border-radius: .625em;
    outline: 0;
    font-size: 1.6em;
    font-weight: 600;
    font-family: "Open Sans";
    cursor: pointer;
}

.main:hover{
    opacity: var(--opacity-button);
}

.main:active{
    opacity: var(--opacity-button-active);
}

.main:disabled{
    opacity: var(--opacity-button-disabled);
}