
.inputPanel, .main{
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    color: var(--secondary-color);
}

.main{
    width: 100%;
    height: 100%;
}

.inputPanel{
    flex: 1;
    flex-direction: column;
    margin: 0 1.5em;
}

.varsPanel{
    min-height: 100%;
    width: 0rem;
    transition: width .5s;
    position: relative;
    box-sizing: border-box;
    background-color: var(--primary-color);
    --w: min(35rem, 500px);
}

.varsPanel::before{
    content: "";
    width: 1px;
    height: 100%;
    background-color: var(--secondary-color);
    position: absolute;
    right: -1px;
}

.varsPanel.varsActive{
    width: var(--w);
}

@media only screen and (max-width: 600px) {
    
}

.varsPanel .cut{
    display: block;
    position: absolute;
    width: 100%;
    height: 100%;
    overflow: hidden;
}

.varsPanel .container{
    width: var(--w);
    min-height: 100%;
    left: 0;
    box-sizing: border-box;
}

.varsPanel{
    content: "";
}

.varsPanelTab{
    position: absolute;
    top: 40%;
    height: 20%;
    width: 40px;
    border: 1px var(--secondary-color) solid;
    border-left: 1px var(--secondary-color) dotted;
    background-color: var(--primary-color);
    right: -40px;
    color: var(--primary-textColor);
}

@media only screen and (max-width: 1500px) {
    .inputPanel{
        font-size: 1em;
        margin: 0 3em;
    }
    .varsPanel{
        --w: max(35rem, 300px);
    }
    
    .inputPanel > div{
        display: flex;
        flex-direction: column;
        justify-content: center;
        flex-wrap: wrap;
    }

    .inputPanel button{
        margin: 0.25em;
        margin-top: .5em;
        flex-grow: 1;
    }

    .inputPanel{
        position: relative;
        margin-left: 50px;
    }
}

@media only screen and (max-width: 1024px) {
    .varsPanel{
        position: fixed;
        z-index: 1;
        left: 0;
        --w: 50vw;
    }
    .inputPanel{
        position: relative;
    }
}

@media only screen and (max-width: 640px) {
    .varsPanel{
        position: fixed;
        z-index: 1;
        left: 0;
        --w: calc(100vw - 50px)
    }
    
    .inputPanel > div{
        display: flex;
        flex-direction: column;
        justify-content: center;
        flex-wrap: wrap;
    }

    .inputPanel button{
        margin: 0.25em;
        margin-top: .5em;
        flex-grow: 1;
    }

    .inputPanel{
        position: relative;
        margin-left: 50px;
        font-size: .75em;
    }
}

.inputPanel h1{
    margin: 1em 0 .25em 0;
}

.inputPanel h1+div{
    font-size: 1.5em;
}

.error{
    margin: 1em 0 .25em 0;
    color: var(--error-textColor);
}

.errorSub{
    color: var(--error-textColor);
    font-weight: 400;
}